@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);

@media screen and (min-width:1080px){
    .send-to-right{
        float:right;
    }
    
    .set-margin {
        margin-left: 0px;
    }
    
    .set-style {
        font-size: 20px;
        font-family: Arial, Helvetica, sans-serif;
        color:white;
    }
    
    ul li {
        display: block;
        color:white;
        list-style: none;
        height: 100%;
        padding-top:.5%;
        
    }
    
    ul li + li {
        margin-right: 2%;
    }
    
    ul li:first-child{
       padding-right: 3%; 
    }
    
    ul{
        width: 100%;
        height: 100%;
        margin-top:0px;
    }
    li:hover a {
        color:lightblue;
        transition: .7s ease all;
    }
    
    a{
        text-decoration: none;
    }
    
    
    #section-navbar {
        position:absolute;
        top:0%;
        left:0%;
        right:0%;
        bottom:0%;
        background-color: black;
        width: 100%;
        height: 5%;
    }
}


/*Font imports*/


@media screen and (min-width:1600px){
    #section-title{
        background-image: url(/barbieri-dev.github.io/static/media/computer-high-resolution.fbe41b05.jpg);
        width: 101.45%;
        height: 700px;
        margin-top:0%;
        margin-left:-1%;
        padding-top: 10%;
    }

    #section-title > #name-container  {
        font-size: 35px;    
        color:white;
        background-color: black;
        width: 50%;
        height: 30%;
        padding-top: 1%;
        margin-left:auto;
        margin-right:auto;
        font-family: 'Open Sans Condensed', sans-serif;
    }

    #name-container * {
        width: 30%;
        text-align: center;
        margin-left: auto;
        margin-right:auto;
        line-height: 60%;
    }
   
}
/*Font imports*/

@media screen and (min-width:1600px){
    p,h2, h3{
        font-family: 'Open Sans Condensed', sans-serif;
        color:white;
    }
    
    .all-width {
        width: 100.87%;
        margin-left: -.50%;
        margin-top: -2%;
    }   
    
    
    /*ABOUT ME CODE*/
    #aboutMe { /*ABOUT ME CONTENT CONTAINER*/
        background-color: black;
    }


 
    #aboutMe #description-container {
        box-sizing: border-box;
        width: 50%;
        margin-right: auto;
        margin-left: auto;
        text-align: center;
        padding-top: 5%;
        padding-bottom:6%;
    }
    #aboutMe #description-container #title{
        font-size: 45px;
    }

    #aboutMe #description-container p {
        font-size: 30px;
    }

    /*SERVICES CODE*/

    #services { /*services container*/
        background-image: url(/barbieri-dev.github.io/static/media/blue-solid-background-1080p.d8feb077.jpg);
        padding-bottom: 5%;
        z-index: 0;
    }

    #services #services-title {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        padding-top: 2%;
    }

    #services #services-title h2 {
        font-size: 45px;
    }

    /*Cards*/

    #services #services-cards-container{
        margin-left:auto;
        margin-right: auto;
        display: flex;
        flex-wrap: wrap;
        grid-row-gap: 50%;
        row-gap: 50%;
    }
}
img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 1%;
    width: 75%;
}

.cards-size {
    width: 15%;
    z-index: 1;
    background-color: lightgray;
    border-top-left-radius: 5%;
    border-top-right-radius: 5%;
    padding-top: .3%;
    border: solid 1px black;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5%;
    flex-basis: 25%;
    box-shadow: 3.5px 3.5px 30px black;
}

.cards-image-size {
    background-color: white;
    width: 30%;
    padding: 5%;
    border-radius: 50%;
    margin-bottom: 10%;
    border: solid 1px black;
    z-index: 0;
    margin-left: auto;
    margin-right: auto;
}

.cards-p-size p {
    font-size: 20px;
    z-index: 1;
    color:black;
    background-color: white;
    margin-top: -11%;
    padding-top: 5%;
    padding-bottom: 2%;
    text-align: center;
    margin-bottom: 0%;
    white-space: pre-line;
}

.cards-h3-size h3 {
    text-align: center;
    margin-top: -9%;
    font-size: 30px;
    z-index: 0;
    color:black;
    background-color: white;
    border-top: solid 1px black;
    padding-top: 3%;
}


@media screen and (min-width: 1600px){
    
    
    
    
    #contact-container {
        background-image: url(/barbieri-dev.github.io/static/media/worldwide-1080p.19acb76a.jpg);
        width: 101.4%;
        margin-left: -1%;
        padding-top:1%;
        padding-bottom: 10%;
    }

    
    #contact-container h3{
        font-size: 48px;
        text-align: center;
    }


    /*FORM RELATED*/
    textarea {
        resize: none;
    }


    #contact-container #div-form {
        width: 100%;
        font-family: Arial, Helvetica, sans-serif;
        font-size: 24px;
        color:white;
    }

   /*EACH INPUT CONTAINERS*/

    label {
        font-family: 'Open Sans Condensed', sans-serif;
        font-size: 32px;
        font-weight: bold;
    }

    input {
        height: 35px;
        margin-left: 1%;
        font-size: 18px;
        width: 300px;
        border: solid 1px black;    
        border-radius: 25px;
        padding-right: 1%;
        padding-left: 1%;
    }
    textarea{
        padding-left: 2%;
        padding-right: 2%;
        border-radius: 25px;
        padding-top: 1%;
        padding-bottom: 1%;
    }


    input:hover, textarea:hover{
        border: solid 2px lightblue;
        border-radius: 25px;
    }
    input:focus{
        border-radius: 25px;
        outline: none;
    }
    textarea:focus {
        outline: none;
        border-radius: 25px;
    }



    #div-nombre{
        margin-bottom: 2%;
        margin-top: 3%;
    }

    #div-nombre input {
        margin-right: -1.1%;
    }

    #div-apellido {
        margin-top: 2%;
    }

    #div-correo {
        margin-top: 2%;
        margin-bottom: 2%;
    }

    #div-correo input {
        margin-right:-2.5%;
    }

    #div-mensaje {
        margin-top: 2%;
    }
    #lbl_mensaje{
        float:left;
        margin-left: 27%;
        
    }
    #mensaje{
        margin-left:-8%;
    }
    
    #submit {
        margin-top: 3%;
        margin-bottom: 5%;  
        border-radius: 25px;
        width: 450px;
        height: 50px;
        outline: none;
        border: solid 1px white;
        font-family: 'Open Sans Condensed', sans-serif;
        font-size: 24px;
        font-weight: bold;
        background-color: transparent;
        color: white;
    }

    #submit:hover {
        border: solid 1px lightblue;
        color:lightblue;
        width: 452px;
        height: 52px;
        cursor: pointer;
        
    }

    textarea {
        width: 50%;
        font-family: Arial, Helvetica, sans-serif;
        font-size: 14px;
    }


   
    form {
        margin-top: 5%;
        margin-bottom:5%;
        width: 50%;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        border-radius: 5%;
        border: solid 2px white;
    }


}
